import React from "react";
import Grid from '@mui/material/Grid';

import Factsheet from './components/factsheet.js'
import CustomCard from './components/customCard.js'

import ApolloClient from 'apollo-boost';
import { ApolloProvider } from 'react-apollo';

import './styles/App.css';


const client = new ApolloClient({
  uri: 'http://localhost:8000/graphql/',
});

function App() {

  const [fs, setFs] = React.useState(1);

  const handleSave = (newGraph) => {
  };
  const handleNewFactsheet = (fs) => {
    setFs(fs);
  };

  return (
    <ApolloProvider client={client}>
      <div >
        <Grid container spacing={2} >
          {!fs && <Grid container style={{ 'padding' : '200px' }}>
            <Grid item xs={2}>
            </Grid>
            <Grid item xs={3}>
            </Grid>
            <Grid item xs={3}>
              <CustomCard top_img="kg-card-6.png" title="Factsheets" create_new={handleNewFactsheet} create_new_button={true} />
            </Grid>
            <Grid item xs={3}>
            </Grid>
            <Grid item xs={2}>
            </Grid>
          </Grid>}
          {fs && <Grid item xs={12}>
            <Factsheet onChange={handleSave} />
          </Grid>}
      </Grid>
    </div>
    </ApolloProvider>
  );
}

export default App;
